@import "./src/assets/sass/prepends.scss";

section {
  display: block;
  margin: .4em 0;
  padding: .5em;
  color: $text-black;
  
  > * {
    margin: .5em;
  }
  > div {
    display: flex;
    align-items: center;
    
    > * {
      display: inline-block;
    }
    time {
      font-size: .75em;
      margin-right: .5em;
      flex-grow: 0.1;
      flex-shrink: 0;
      flex-basis: auto;
    }
    span {
      flex-grow: 1;
      flex-shrink: 1;
      flex-basis: auto;
    }
    button {
      background-color: transparent;
      border: none;
      width: 16px;
      height: 16px;
      margin-right: .5em;
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: 16px;
    }
  }
  > h4 {
    display: block;
    font-size: 1.25em;
    // color: $text-link-blue;
    text-decoration: underline;
  }

  > p {
    display: block;
    line-height: 1.25;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}

