@import "./src/assets/sass/prepends.scss";

article {
  padding: 1rem 0;
  header {
    height: auto;
    > div {
      display: flex;
      align-items: center;
      margin: 0.75rem 0;

      > * {
        display: inline-block;
      }
      time {
        font-size: 0.75em;
        margin-right: 0.5em;
        flex-grow: 0.1;
        flex-shrink: 0;
        flex-basis: auto;
      }
      span {
        flex-grow: 1;
        flex-shrink: 1;
        flex-basis: auto;
      }
      button {
        background-color: transparent;
        border: none;
        width: 16px;
        height: 16px;
        margin-right: 0.5em;
        flex-grow: 0;
        flex-shrink: 0;
        flex-basis: 16px;
      }
    }
  }
  div.message-content {
    margin: 1rem 0;
    padding: 0.5rem;
  }
}
