@import "./src/assets/sass/prepends.scss";

ul {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin: 3rem 0 1rem;
  padding: 0;
  // background-color: $bg-dark;
  border-radius: 1rem;
  li {
    list-style: none;
    display: inline-block;
    box-sizing: border-box;
    text-align: center;
    margin: 0.5rem;
    padding: 0.65rem 0.4rem;
    min-width: 2.5rem;
    min-height: 2.5rem;
    line-height: 100%;
    font-size: 1rem;
    border-radius: 50%;
    background-color: $bg-light-lighten3;
    cursor: pointer;

    &.circle {
      min-width: 0.4rem;
      min-height: 0.4rem;
      padding: 0.1rem;
      cursor: default;
    }
    &.disabled {
      color: $text-dark;
    }
    &.active {
      background-color: $bg-light;
    }
  }
}
