@import "./src/assets/sass/prepends.scss";

form {
  margin-bottom: 1em;
}

.formtext {
  display: inline-block;
  box-sizing: border-box;
  height: 2rem;
  width: calc(100% - 4rem);
  min-width: 10rem;
  max-width: 33rem;
  padding: 0.125rem 0.25rem;
  margin-right: 0.5rem;
  margin-top: 0.5rem;
}
.search {
  display: inline-block;
  box-sizing: border-box;
  height: 2rem;
  width: 3.5rem;
  padding: 0.125rem 0.5rem;
  margin-top: 0.5rem;
}

label {
  display: inline-flex;
  font-size: 0.75rem;
  margin-right: 1rem !important;
  align-items: flex-end;

  input[type="checkbox"] {
    display: inline-block;
    margin-left: 0.2rem;
  }
}
